// 
import { message } from 'antd'

export function addItemToTree<T extends { key: string; children?: T[] }>(
  items: T[],
  curKey: string | null,
  newItem: T
): T[] {
  //  newItem  key 
  const isKeyExists = (items: T[], key: string): boolean => {
    return items.some(
      item => item.key === key || (item.children && isKeyExists(item.children, key))
    )
  }

  if (isKeyExists(items, newItem.key)) {
    message.error('key ')
    return items
  }

  if (curKey === null) {
    //  curKey  null newItem 
    return [...items, newItem]
  }

  //  key 
  return items.map(item => {
    if (item.key === curKey) {
      //  children
      return {
        ...item,
        children: [...(item.children || []), newItem]
      }
    } else if (item.children) {
      // 
      return {
        ...item,
        children: addItemToTree(item.children, curKey, newItem)
      }
    }
    return item
  })
}

// 
export function removeItemFromTree<T extends { key: string; children?: T[] }>(
  items: T[],
  curKey: string
): T[] {
  return items
    .filter(item => item.key !== curKey) //  key 
    .map(item => ({
      ...item,
      children: item.children ? removeItemFromTree(item.children, curKey) : [] // 
    }))
}
